import React from 'react';

import { Link as RouterLink} from "react-router-dom";

const gaPageView = (to) => {
  // window.dataLayer.push('set', 'page', to); 
  // ga('set', 'page', to);

  window.dataLayer.push({
    'event':'VirtualPageview',
    'virtualPageURL': to,
    });
}

const Link = ({
  to,
  className,
  children
}) => (
  <RouterLink className={className} to={to} onClick={() => gaPageView(to)}>{children}</RouterLink> );

export default Link;

